    </base-header>
    <div class="container-fluid mt--7">
      <div class="row">
        <div class="col">
          <div class="card shadow" :class="type === 'dark' ? 'bg-default': ''">
            <div class="card-header border-0" :class="type === 'dark' ? 'bg-transparent': 'black'">
              <div>
                <div class="container my-2">
                  <strong
                    style="font-size:2em"
                    class="mb-0"
                    :class="type === 'dark' ? 'text-white': ''"
                  >Pagar Assinatura</strong>

                 
                  <form>
                    <div class="row my-3">
                      <div class="col-6 col-md-6">
                        <div>
                          <b-button
                            class="btn-primary mb-3 btn-block"
                            size="md"
                            type="submit"
                            variant="primary"
                            :disabled="loading"
                            @click.prevent="buscarCaixa(1)"
                          >Hoje</b-button>
                        </div>
                        <div>
                          <b-button
                            class="btn-primary mb-3 btn-block"
                            size="md"
                            type="submit"
                            variant="primary"
                            :disabled="loading"
                            @click.prevent="buscarCaixa(3)"
                          >30 dias</b-button>
                        </div>
                        <validation-provider
                          name="filtro"
                          :rules="{ required: false, }"
                          v-slot="validationContext"
                        >
                          <strong>De:</strong>
                          <b-form-input
                            type="tel"
                            class="form-control"
                            name="dataInicial"
                            placeholder="dd/mm/aaaa"
                            v-mask="'##/##/####'"
                            v-model="dataInicial"
                            :state="getValidationState(validationContext)"
                            aria-describedby="input-1-live-feedback"
                          />
                          <b-form-invalid-feedback
                            id="input-1-live-feedback"
                          >{{ validationContext.errors[0] }}</b-form-invalid-feedback>
                        </validation-provider>
                        <b-button
                          class="btn-primary mt-3 btn-block"
                          size="md"
                          type="submit"
                          variant="primary"
                          :disabled="loading"
                          @click.prevent="buscarCaixa(5)"
                        >Buscar</b-button>
                      </div>

                      <div class="col-6 col-md-6">
                        <div>
                          <b-button
                            class="btn-primary mb-3 btn-block"
