                        </div>
                        <div>
                          <b-button
                            class="btn-primary mb-3 btn-block"
                            size="md"
                            type="submit"
                            variant="primary"
                            :disabled="loading"
                            @click.prevent="buscarCaixa(4)"
                          >60 dias</b-button>
                        </div>
                        <validation-provider
                          name="filtro"
                          :rules="{ required: false, }"
                          v-slot="validationContext"
                        >
