            <div class="table-responsive">
              <base-table
                class="table align-items-center table-flush"
                :class="type === 'dark' ? 'table-dark': ''"
                :thead-classes="type === 'dark' ? 'thead-dark': 'thead-light'"
                tbody-classes="list"
                :data="caixas"
              >
                <template slot="columns">
                  <th>Boleto</th>

                  <th>Numero</th>
                  <th>Vencimento</th>
                  <th>Código de Barras</th>
                  <th>Plano</th>
                  <th>Forma de Pagamento</th>
                  <th>Status</th>
                  <th>Valor</th>

                  <th></th>
                </template>

                <template slot-scope="{row}">
                 
                   <td class="name mb-0 text-sm text-700">
                     <a :href="`${row.urlBoleto}`" target="_blank" class="avatar rounded-circle mr-3">
                        <b-img  src="../imgsistema/codigo-de-barras.svg" />
                      </a>
                  </td>
                  <td class="name mb-0 text-sm text-700">
                    {{ row.id}}
                  </td>
                  <td class="name mb-0 text-sm text-700">
                    {{ row.dataFechamento}}
                  </td>
