</template>
<script>
import moment from "moment";
import Auth from "../../src/auth";
import axios from "axios";
import config from "../config";
import Swal from "sweetalert2";

export default {
  data() {
    return {
      Auth: Auth,
      config: config,
      caixas: [],
      codigoEmpresa: "",
      dataInicial: "",
      dataFinal: "",
      nome: "",
      cpF_CNPJ: "",
      qtdCaixas: 0,
      entrada: 0,
      saida: 0,
      final: 0,
      hoje: false,
      ontem: false,
      mesAtual: false,
      mesAnterior: false,
      personalizada: false,
      loading: false,
    };
  },
  created: function () {
    //Se não tem o usuario, vai para a tela principal

    if (!Auth.isLoggedIn()) {
      this.$router.replace("/login");
    }
    if (this.Auth.isLoggedIn()) {
      this.myFunction();
    }
    this.buscarCaixa(5);
  },
  methods: {
    getValidationState({ dirty, validated, valid = null }) {
      return dirty || validated ? valid : null;
    },
    myFunction() {
      setTimeout(function () {
        this.Auth.logout(),
          location.reload(),
          alert("Tempo limite excedido!Por favor, faça login novamente!");
      }, 2400000);
    },
    async buscarCaixa(num) {
      let timerInterval;
      Swal.fire({
        title: "Por favor Aguarde...",
        timer: 2000000,
        timerProgressBar: false,
        onBeforeOpen: () => {
          Swal.showLoading();
          timerInterval = setInterval(() => {
            const content = Swal.getContent();
            if (content) {
              const b = content.querySelector("b");
              if (b) {
                b.textContent = Swal.getTimerLeft();
              }
            }
          }, 100);
        },
        onClose: () => {
          clearInterval(timerInterval);
        },
      }).then((result) => {
        /* Read more about handling dismissals below */
        if (result.dismiss === Swal.DismissReason.timer) {
          console.log("I was closed by the timer");
        }
      });
      var data;
      var dataInicial;
      var dataFinal;
      if (num == 1) {
        this.hoje = true;
        this.ontem = false;
        this.mesAtual = false;
        this.mesAnterior = false;
        this.personalizada = false;
        this.entrada = 0;
        this.saida = 0;
        this.qtdCaixas = 0;
        this.final = 0;
        dataInicial = "";
        dataFinal= "";
        this.dataInicial = "";
        this.dataFinal = "";
        this.codigoEmpresa = Auth.getUserInfo().codigoEmpresa;
        this.caixas = [];
        data = {
          codigoEmpresa: this.codigoEmpresa,
          dataInicial: this.dataInicial,
          dataFinal: this.dataFinal,
          nome: " ",
          cpF_CNPJ: " ",
        };
      }

      if (num == 2) {
        this.caixas = [];
        this.hoje = false;
        this.ontem = true;
        this.personalizada = false;
        this.mesAtual = false;
        this.mesAnterior = false;
        this.entrada = 0;
        this.saida = 0;
        this.qtdCaixas = 0;
        this.final = 0;
        // var dataInicial = moment().format("l");
        dataInicial = moment().subtract(1, "days").startOf("day");
        dataFinal = moment().subtract(1, "days").startOf("day");
        this.dataInicial = moment(dataInicial).format("DD/MM/YYYY");
        this.dataFinal = moment(dataFinal).format("DD/MM/YYYY");
        this.codigoEmpresa = Auth.getUserInfo().codigoEmpresa;

        data = {
          codigoEmpresa: this.codigoEmpresa,
          dataInicial: this.dataInicial,
          dataFinal: this.dataFinal,
          nome: " ",
          cpF_CNPJ: " ",
        };
      }

      if (num == 3) {
        this.caixas = [];
        this.hoje = false;
        this.ontem = false;
        this.personalizada = false;
        this.mesAtual = true;
        this.mesAnterior = false;
        this.entrada = 0;
        this.saida = 0;
        this.qtdCaixas = 0;
        this.final = 0;
        // var dataInicial = moment().format("l");
        dataInicial = moment().subtract(30, "days").startOf("day");
        dataFinal = moment().format();
        this.dataInicial = moment(dataInicial).format("DD/MM/YYYY");
        this.dataFinal = moment(dataFinal).format("DD/MM/YYYY");

        this.codigoEmpresa = Auth.getUserInfo().codigoEmpresa;

        data = {
          codigoEmpresa: this.codigoEmpresa,
          dataInicial: this.dataInicial,
          dataFinal: this.dataFinal,
          nome: " ",
          cpF_CNPJ: " ",
        };
      }

      if (num == 4) {
        this.caixas = [];
        this.hoje = false;
        this.ontem = false;
        this.mesAtual = false;
        this.personalizada = false;
        this.mesAnterior = true;
        this.entrada = 0;
        this.saida = 0;
        this.final = 0;
        this.qtdCaixas = 0;
        // var dataInicial = moment().format("l");
        dataInicial = moment().subtract(60, "days").startOf("day");
        dataFinal = moment().format();
        this.dataInicial = moment(dataInicial).format("DD/MM/YYYY");
        this.dataFinal = moment(dataFinal).format("DD/MM/YYYY");
        this.codigoEmpresa = Auth.getUserInfo().codigoEmpresa;

        data = {
          codigoEmpresa: this.codigoEmpresa,
          dataInicial: this.dataInicial,
          dataFinal: this.dataFinal,
          nome: " ",
          cpF_CNPJ: " ",
        };
      }
      if (num == 5) {
        this.caixas = [];
        this.hoje = false;
        this.ontem = false;
        this.personalizada = true;
        this.mesAtual = false;
        this.mesAnterior = false;
        this.qtdCaixas = 0;
        this.entrada = 0;
        this.saida = 0;
        this.final = 0;
        // var dataInicial = moment().format("l");
        dataInicial = this.dataInicial;
        dataFinal = this.dataFinal;

        this.codigoEmpresa = Auth.getUserInfo().codigoEmpresa;

        data = {
          codigoEmpresa: this.codigoEmpresa,
          dataInicial: dataInicial,
          dataFinal: dataFinal,
          nome: " ",
          cpF_CNPJ: " ",
        };
      }

      try {
        var response = await axios.post(
          `${config.API_URL}/Pagamento/Pesquisar`,
          data
        );

        var caixas = response.data.pagamentos;
        for (let index = 0; index < caixas.length; index++) {       
        caixas[index].dataFechamento = moment(caixas[index].dataFechamento).format("DD/MM/YYYY");    
        }


        Swal.fire({
          position: "center",
          icon: "success",
          title: "Busca efetuada com Sucesso!",
          showConfirmButton: false,
          timer: 1500,
        });
        return (this.caixas = caixas), this.qtdCaixas;
      } catch (error) {
        Swal.fire({
          position: "center",
          icon: "warning",
          title: "Nenhum registro encontrado!",
          showConfirmButton: false,
          timer: 1500,
        });
        return error;
      }
    },
  },
};
</script>
<style>
th {
  color: black !important;
}
</style>
