<template>
  <div class="row justify-content-center">
    <div class="col-lg-5 col-md-7">
      <div class="card bg-white shadow border-0">
        <div class="card-header bg-transparent pb-3">
          <div class="text-muted text-center mt-4 ">
            <div>
              <b-img src="/imgsistema/logo-pdvende-2.png" fluid alt="Fluid image" width="200"></b-img>
            </div>
          </div>
          <!-- <div class="btn-wrapper text-center">
            <a href="#" class="btn btn-neutral btn-icon">
              <span class="btn-inner--icon">
                <img src="img/icons/common/github.svg" />
              </span>
              <span class="btn-inner--text">Github</span>
            </a>
            <a href="#" class="btn btn-neutral btn-icon">
              <span class="btn-inner--icon">
                <img src="img/icons/common/google.svg" />
              </span>
              <span class="btn-inner--text">Google</span>
            </a>
          </div>-->
        </div>
        <div class="card-body px-lg-5 py-lg-5">
          <div class="text-center text-muted mb-4">
            <h3>Faça seu cadastro agora mesmo!</h3>
          </div>
           <section>
      <b-container>
        <validation-observer ref="observer" v-slot="{ handleSubmit }">
          <b-form class="form-signin" @submit.stop.prevent="handleSubmit(onClickLogin)">
            <validation-provider
              name="Email"
              :rules="{ required: true, email: true }"
              v-slot="validationContext"
            >
              <b-form-group id="example-input-group-1" label-for="example-input-1">
                <b-form-input
                  type="email"
                  id="inputEmail"
                  placeholder="Digite um e-mail que você usa diariamente"
                  v-model="Email"
                  :state="getValidationState(validationContext)"
                  aria-describedby="input-1-live-feedback"
                />

                <b-form-invalid-feedback
                  id="input-1-live-feedback"
                >{{ validationContext.errors[0] }}</b-form-invalid-feedback>
              </b-form-group>
            </validation-provider>

            <validation-provider
              name="Nome Completo"
              :rules="{ required: true, min:3 }"
              v-slot="validationContext"
            >
              <b-form-group>
                <b-form-input
                  type="text"
                  id="inputRazaoSocial"
                  placeholder="Nome completo"
                  v-model="NomeCompleto"
                  :state="getValidationState(validationContext)"
                  aria-describedby="input-1-live-feedback"
                />

                <b-form-invalid-feedback
                  id="input-1-live-feedback"
                >{{ validationContext.errors[0] }}</b-form-invalid-feedback>
              </b-form-group>
            </validation-provider>

            <validation-provider
              name="Telefone"
              :rules="{ required: true, min:11 }"
              v-slot="validationContext"
            >
              <b-form-group>
                <b-form-input
                  type="tel"
                  id="inputTelefone"
                  placeholder="Numero do seu telefone"
                  v-mask="['(##) ####-####', '(##) # ####-####']"
                  v-model="Telefone"
                  :state="getValidationState(validationContext)"
                  aria-describedby="input-1-live-feedback"
                />

                <b-form-invalid-feedback
                  id="input-1-live-feedback"
                >{{ validationContext.errors[0] }}</b-form-invalid-feedback>
              </b-form-group>
            </validation-provider>
            <validation-provider
              name="Senha"
              :rules="{ required: true, min:3 }"
              v-slot="validationContext"
            >
              <b-form-group>
                <b-form-input
                  type="password"
                  id="inputPassword"
                  placeholder="Digite uma senha"
                  v-model="senha"
                  required
                  :state="getValidationState(validationContext)"
                  aria-describedby="input-1-live-feedback"
                />

                <b-form-invalid-feedback
                  id="input-1-live-feedback"
                >{{ validationContext.errors[0] }}</b-form-invalid-feedback>
              </b-form-group>
            </validation-provider>

            <b-button
              class="btn btn-lg btn-primary btn-block b-2"
              type="submit"
              variant="primary"
              :disabled="loading"
            >Criar Conta</b-button>
            <router-link to="/login"  style="font-size:15px; margin-right:3px;">Retornar para Login</router-link>
            <div class="my-3">
              <p style="color:black; height:400">© 2015-2020</p>
            </div>
          </b-form>
        </validation-observer>
      </b-container>
    </section>
        </div>
      </div>
      <!-- <div class="row mt-3">
        <div class="col-6">
          <a href="#" class="text-light">
            <small>Forgot password?</small>
          </a>
        </div>
        <div class="col-6 text-right">
          <router-link to="/register" class="text-light">
            <small>Create new account</small>
          </router-link>
        </div>
      </div> -->
    </div>
  </div>
  
</template>
<script>
import Swal from "sweetalert2";
import axios from "axios";

import Auth from "../auth";
import config from "../config";

export default {
  data() {
    return {
      config,
      loading: false,
      Email: "",
      NomeCompleto: "",
      Telefone: "",
      senha: "",
      qtdErroLogin: 0
    };
  },
  methods: {
    getValidationState({ dirty, validated, valid = null }) {
      return dirty || validated ? valid : null;
    },
    resetForm() {
      this.form = {
        name: null,
        food: null
      };

      this.$nextTick(() => {
        this.$refs.observer.reset();
      });
    },
    async onClickLogin() {
      this.loading = false;

      try {
        var data = {
          Email: this.Email,
          NomeCompleto: this.NomeCompleto,
          Telefone: this.Telefone,
          senha: this.senha
        };
        let timerInterval;
        Swal.fire({
          title: "Por favor aguarde...!",
          timer: 20000,
          timerProgressBar: true,
          onBeforeOpen: () => {
            Swal.showLoading();
            timerInterval = setInterval(() => {
              const content = Swal.getContent();
              if (content) {
                const b = content.querySelector("b");
                if (b) {
                  b.textContent = Swal.getTimerLeft();
                }
              }
            }, 100);
          },
          onClose: () => {
            clearInterval(timerInterval);
          }
        }).then(result => {
          /* Read more about handling dismissals below */
          if (result.dismiss === Swal.DismissReason.timer) {
            console.log("I was closed by the timer");
          }
        });
        var response = await axios.post(
          `${config.API_URL}/Empresa/CriarConta`,
          data
        );
       

        Swal.fire({
          position: "center",
          icon: "success",
          title:"Empresa cadastrada com sucesso!",
          text:
            "Olá, seja bem vindo ao PDVende, o Sistema de Gestão Controle Empresarial com o melhor custo-benefício do mercado!",
          showConfirmButton: true,
          timer: 20000
        });

        // Swal.fire({
        //   position: "center",
        //   icon: "success",
        //   title: "Ok!",
        //   showConfirmButton: false,
        //   timer: 2000
        // });
        Auth.login(
          response.data.token,
          response.data.dtExpiry,
          response.data.userInfo,
          response.data.assinaturaEmpresa
        );      
          this.$router.replace("/dashBoard");
      
      } catch (error) {
        console.error(error);
        this.loading = false;
        var mensagem = error.response.data.erros.mensagem;

        Swal.fire({
          icon: 'error',
          type: "error",
          title: "Oops...",
          text: `${mensagem}`
        });
        this.qtdErroLogin++;
        if (this.qtdErroLogin >= 2) {
          this.$router.push("/cadastrarEmpresa");
        }

        return;
      }
    }
  }
};
</script>
